
'use client';

import React, { useState, useEffect, useCallback } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { LogOut, Search, ShieldAlert, Home } from 'lucide-react';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { auth, db } from '@/lib/firebase';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import AdminAuthPage from './auth/page';
import { doc, getDoc } from 'firebase/firestore';
import { Loader2 } from 'lucide-react';

const ADMIN_AUTH_TIMEOUT = 5 * 60 * 1000; // 5 minutes

export default function AdminLayout({ children }: { children: React.ReactNode }) {
    const user = auth.currentUser;
    const router = useRouter();
    const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null);
    const [userRole, setUserRole] = useState<string | null>(null);

    const verifyAccess = useCallback(async () => {
        const authTimestampStr = sessionStorage.getItem('adminAuthTimestamp');
        if (authTimestampStr && Date.now() - parseInt(authTimestampStr, 10) < ADMIN_AUTH_TIMEOUT) {
            if (user) {
                try {
                    const userDoc = await getDoc(doc(db, 'users', user.uid));
                    if (userDoc.exists()) {
                        const role = userDoc.data().role;
                        setUserRole(role);
                        if (role === 'admin') {
                            setIsAuthenticated(true);
                            return;
                        }
                    }
                } catch (error) {
                    console.error("Error verifying user role:", error);
                }
            }
        }
        setIsAuthenticated(false);
    }, [user]);

    useEffect(() => {
        const unsubscribe = auth.onAuthStateChanged((user) => {
            if (user) {
                verifyAccess();
            } else {
                setIsAuthenticated(false);
            }
        });
        return () => unsubscribe();
    }, [verifyAccess]);

    const handleAuthentication = () => {
        sessionStorage.setItem('adminAuthTimestamp', Date.now().toString());
        setIsAuthenticated(true);
    };

     if (isAuthenticated === null) {
        return <div className="flex h-screen w-full items-center justify-center"><Loader2 className="w-12 h-12 animate-spin text-primary" /></div>;
    }

    if (!isAuthenticated) {
        return <AdminAuthPage onAuthenticated={handleAuthentication} />;
    }

    return (
        <div className="min-h-screen w-full bg-muted/40">
              <div className="flex flex-col">
                <header className="flex h-14 items-center gap-4 border-b bg-background px-4 lg:h-[60px] lg:px-6 sticky top-0 z-30">
                  <div className="flex items-center gap-2">
                     <Link href="/admin" className="flex items-center gap-2 text-lg font-semibold">
                        <ShieldAlert className="h-6 w-6 text-primary" />
                        <span className="font-bold text-foreground">Admin Paneli</span>
                    </Link>
                  </div>
                  <div className="w-full flex-1">
                     <div className="relative">
                        <Search className="absolute left-2.5 top-2.5 h-4 w-4 text-muted-foreground" />
                        <Input
                            type="search"
                            placeholder="Ara..."
                            className="w-full appearance-none bg-muted shadow-none md:w-2/3 lg:w-1/3"
                        />
                    </div>
                  </div>
                  <div className="flex items-center gap-2">
                        <Link href="/explore">
                            <Button variant="outline" size="icon" className="h-9 w-9">
                                <Home className="h-4 w-4" />
                                <span className="sr-only">Ana Sayfaya Dön</span>
                            </Button>
                        </Link>
                        {user?.photoURL && (
                            <Avatar className="w-9 h-9">
                                <AvatarImage src={user.photoURL} />
                                <AvatarFallback>{user.displayName?.charAt(0) ?? 'A'}</AvatarFallback>
                            </Avatar>
                        )}
                    </div>
                </header>
                <main className="flex flex-1 flex-col gap-4 p-4 lg:gap-6 lg:p-6">
                  {children}
                </main>
              </div>
        </div>
    );
}
